import React from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, onlyAlphaNum, onlyNumber } from 'summer';
import { ConfigIcmsDifalUF } from '../../../../models/dtos/mlf/configIcmsDifalUF';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const emptyItem: ConfigIcmsDifalUF = {
  codigoReceita: '',
  variacaoCodigoReceita: '',
};

const ConfigIcmsDifalUFDetailEditView: React.FC<DetailEditViewFormProps<ConfigIcmsDifalUF>> = props => {
  const { errors, getValues, register, setValue, triggerValidation } = useForm<ConfigIcmsDifalUF>({
    defaultValues: props.selectedItem || emptyItem,
    mode: 'onBlur',
  });

  const formProps = {
    errors,
    getValues,
    register,
    setValue,
    triggerValidation,
  };

  const validateExtras = (currentItem: any, currentValues: any[], parentObject: any) => {
    const erros = [];
    if (currentValues != null && (props.selectedItem == null || props.selectedItem.uf.id !== currentItem.uf.id)) {
      for (const item of currentValues) {
        if (item.uf && item.uf.id === currentItem.uf.id) {
          erros.push('Já existe um código da receita para essa UF.');
          break;
        }
      }
      return erros;
    }
  };
  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <CepUFIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="UF"
            placeholder="Selecione uma UF"
            id="uf"
            isPrependLabel={true}
            inputSearchProps={{ selectProps: { isMulti: false } }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="codigoReceita"
            inputProps={true}
            label="Código da Receita"
            formProps={{ ...formProps, validation: onlyAlphaNum(1, 5) }}
            isPrependLabel={true}
            placeholder="Informe o Código da Receita"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="variacaoCodigoReceita"
            isPrependLabel={true}
            formProps={{ ...formProps, validation: onlyNumber(0, 2) }}
            label="Variação"
            tooltipProps={{ message: 'Variação do código da receita. Esse campo é obrigatório caso a empresa gere o DCTF.' }}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ConfigIcmsDifalUFDetailEditView;
