import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import { ColumnsProps, DetailManagerForm, EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigIcmsDifal } from '../../../../models/dtos/mlf/configIcmsDifal';
import { DIAS_ENUM_VALUES } from '../../../../models/enumerated/ger/diasEnum';
import ConfigIcmsDifalUFDetailEditView from './ConfigIcmsDifalUFDetailEditView';

interface ConfigIcmsDifalProps extends EditViewProps<ConfigIcmsDifal> {}

const emptyItem: ConfigIcmsDifal = {
  notificarVencimento: true,
  notificarVencimentoDias: 2,
  recolhimentoMinimo: 0.0,
};

const ConfigIcmsDifalEditView: React.FC<ConfigIcmsDifalProps & RouteComponentProps> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'uf.sigla',
      sort: true,
      text: 'UF',
    },
    {
      dataField: 'codigoReceita',
      sort: true,
      text: 'Codigo da Receita',
    },
  ];
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigIcmsDifal>({
    defaultValues: selectedItem.id == null ? emptyItem : selectedItem,
    mode: 'onBlur',
  });

  const validateExtras = formValues => {
    const erros = [];
    if (formValues.notificarVencimento && !formValues.notificarVencimentoDias) {
      erros.push('Dias de Antecedência do vencimento - Campo não pode estar em branco.');
    }
    if (formValues.listaConfigUF == null || formValues.listaConfigUF.length === 0) {
      erros.push('Pelo menos 1 código da receita deve ser cadastrado.');
    }
    return erros;
  };

  return (
    <WindowComponent title="Configurações de ICMS">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Configuração de ICMS"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diaVencimento"
              label="Dia de Vencimento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o dia de vencimento"
              inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração de Notificação</CardTitle>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="Notificar Vencimento de Imposto"
                      name="notificarVencimento"
                      id="checkNotificarVencimento"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="notificarVencimentoDias"
                      label="Dias de Antecedência do vencimento"
                      formProps={{ ...formProps, validation: { required: true } }}
                      isPrependLabel={true}
                      disabled={!formProps.getValues().notificarVencimento}
                      placeholder="Informe com quantos de dias de antecedência gostaria de ser notificado"
                      inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      disabled={!formProps.getValues().notificarVencimento}
                      label="Notificar o cliente/responsável por Connect/email, sobre o vencimento do Imposto"
                      name="notificarVencimentoPorEmail"
                      id="checkNotificarVencimentoPorEmail"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="recolhimentoMinimo"
              label="Recolhimento Mínimo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o valor do recolhimento mínimo"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <DetailManagerForm
              columns={columns}
              title="Lista de Código de Receita"
              detailView={ConfigIcmsDifalUFDetailEditView}
              keyField="uf.id"
              formProps={formProps}
              propName="listaConfigUF"
              {...props}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigIcmsDifalEditView;
